import React, { FC } from 'react'
import clsx from 'clsx'
import { Typography } from '../Typography'
import { StickerProps } from './types'
import './Sticker.scss'

const Sticker: FC<StickerProps> = ({
  backgroundColor,
  borderColor = 'transparent',
  className,
  color,
  text,
  itemProp,
  icon = null,
}) => {
  if (!icon) {
    return (
      <Typography
        className={clsx('StickerNext', 'StickerNext__Text', className)}
        itemProp={itemProp}
        style={{ backgroundColor, color, borderColor }}
        text={text}
        type='Caption1Bold'
      />
    )
  }
  return (
    <div
      className={clsx('StickerNext', className)}
      style={{ backgroundColor, color, borderColor }}
    >
      {icon}
      <Typography
        className='StickerNext__Text'
        itemProp={itemProp}
        text={text}
        type='Caption1Bold'
      />
    </div>
  )
}

export default Sticker
